.App {
  text-align: center;
}


.App-logo {
  height: 40vmin;
  pointer-events: none;
}
table.items {
  display: flex;

  margin: 10px;
  align-items: left;
  justify-content: left;
  font-size: 14px;
  color: black;
}

table.items th,
td {
  text-align: center;
  padding: 10px;
}
@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}
.primaryModal
{
position: fixed;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
